
//after o before img
@mixin afterimg {
    content: "";
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    display: block; }
@mixin rotate($rotateValue: 0) {
  transform: rotate($rotateValue + deg); }

.content {
    display: block;
    position: relative;
    top: 0;
    width: 100%;
    width: 95%;
    @include MQ(S) {
        width: 55%; }
    &.full {
        height: 100vh; } }
.card {
    position: absolute;
    top: 114%;
    left: 0;
    display: block;
    width: 95%;
    @include MQ(S) {
        width: 52%; } }
.shears {
    position: absolute;
    top: 46%;
    left: 15%;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/shears.png');
        width: 300px;
        height: 300px;
        transform: rotate(90deg); } }

.clipWoods {
    position: absolute;
    top: 29%;
    left: 18.6%;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/clips_wood.png');
        width: 139px;
        height: 140px;
        transform: rotate(-45deg); } }
.loveWoods {
    position: absolute;
    top: 90%;
    right: 25%;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/loveWoods.png');
        width: 300px;
        height: 180px;
        transform: rotate(5deg); } }

.plants {
    position: absolute;
    top: -50%;
    right: -15%;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/plant.png');
        width: 500px;
        height: 529px;
        transform: rotate(5deg); } }
.burlap.intro {
    top: 10%;
    left: -7%; }
.burlap.open {
    top: 18%;
    left: -6%; }
.burlap.close {
    top: 13%;
    right: 16%; }
.burlap {
    position: absolute;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/burlapped.png');
        width: 700px;
        height: 500px;
        transform: rotate(5deg); } }
.cherry {
    position: absolute; }
.cherry.top {
    top: -8%;
    left: 2%; }
.cherry.bottom {
    bottom: 10%;
    right: 5%; }
.cherry.top {
    &:after {
        @include afterimg;
        background-image: url('../images/theme/cherry-top.png');
        width: 400px;
        height: 323px;
        display: block; } }
.cherry.bottom {
    &:after {
        @include afterimg;
        background-image: url('../images/theme/cherry.png');
        width: 900px;
        height: 641px; } }
.brown {
    position: relative;
    top: 20%;
    left: -3%;
    display: block;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/brown.png');
        width: 800px;
        height: 338px; } }
.heart {
    position: absolute;
    bottom: 39%;
    right: 28%;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/heart_site.png');
        width: 250px;
        height: 205px;
        @include rotate(22); } }
.heart--into {
    position: absolute;
    top: 42%;
    right: 47%;
    z-index: 99;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/heart--into.png');
        width: 252px;
        height: 207px; } }
.green {
    position: absolute;
    &:after {
        @include afterimg;
        background-image: url('../images/theme/green.png'); }
    &.one {
        top: 22%;
        left: 22%;
        &:after {
            width: 250px;
            height: 205px;
            @include rotate(250); } }
    &.two {
        bottom: 25%;
        right: 22%;
        &:after {
            width: 200px;
            height: 164px;
            @include rotate(-10); } } }

//ok
.blackberry {
    position: absolute;
    &:after {
        @include afterimg;
        background-image: url('../images/site/blackberry--site.png'); }
    &.one {
        top: 19%;
        left: 24%;
        &:after {
            width: 242px;
            height: 327px;
            @include rotate(-128); } }
    &.two {
        bottom: 18%;
        right: 29%;
        &:after {
            width: 242px;
            height: 327px; } } }
.ribbon {
    background: $lightPeach;
    position: absolute;
    display: block;
    width: 79%;
    left: 56%;
    top: 43%;
    padding: 5px;
    margin-left: -45%;
    z-index: 10;
    box-shadow: -5px 10px 14px -10px rgba($black, .75);
    .deco {
        border-top: 1.3px dashed $sienna;
        border-bottom: 1.3px dashed $sienna;
        height: 24px; } }

.envelope {
    position: absolute;
    &:after {
        @include afterimg; }
    &.one {
        top: 2%;
        right: 24%;
        &:after {
            background-image: url('../images/site/envelope.png');
            width: 338px;
            height: 650px; } }
    &.two {
        top: 30%;
        left: 20%;
        &:after {
            background-image: url('../images/site/envelope_burlap.png');
            width: 378px;
            height: 480px; } }
    &.open {
        top: 2%;
        right: 24%;
        &:after {
            background-image: url('../images/site/envelope_open.png');
            width: 486px;
            height: 650px; } } }
.perejil {
    position: absolute;
    &:after {
        @include afterimg;
        background-image: url('../images/site/perejil.png'); }
    &.one {
        bottom: 22%;
        left: 0%;
        &:after {
            width: 618px;
            height: 412px; } }
    &.two {
        bottom: 32%;
        right: -3%;
        &:after {
            width: 618px;
            height: 412px; } } }
.fernhome {
    position: absolute;
    &:after {
        @include afterimg; }
    &.one {
        bottom: 25%;
        left: 0%;
        &:after {
            background-image: url('../images/site/fernhome.png');
            width: 408px;
            height: 272px; } }
    &.two {
        top: 20%;
        right: 13%;
        &:after {
            background-image: url('../images/site/fernhome_two.png');
            width: 225px;
            height: 150px; } } }
.into {
    width: 55%;
    height: 160vh;
    display: block;
    position: relative;
    top: -130px;
    left: -45px;
    margin: 0 auto;
    box-shadow: -1px 2px 84px -9px rgba($black, .75);
    @include rotate(15); }
